/** * Created by admin on 2017/6/22. */import React, { Component } from 'react';export default class Hello extends Component {	render() {		return (			<div className="box">				<p className="title">hello sass</p>				<div className="content">					<p className="item"><span className="label">用户名：</span><input type="text" placeholder="请输入用户名"/></p>					<p className="item"><span className="label">密  码：</span><input type="password" placeholder="请输入密码"/></p>				</div>			</div>		)	}}


